<template>
  <div class="hs-container" v-if="!this.noValue(JSON.stringify(details))">
    <c-header isBackNav :title="$t('base.RefundOrderDetails')"></c-header>
    <hs-scroll
      class="table-view"
      :data="[details]"
      :options="tableViewOptions"
      @pulling-down="_getOrderDetails">
      <ul>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="state-details">
              <div class="header">
                <p>{{details.RefundStatusName}}</p>
                <p>{{details.VettingStatusName}}</p>
                <p>{{details.RefundStatusName}}</p>
              </div>
              <p class="infor">{{$t('base.RefundOrderNumber')}}: {{details.RefundNo}}</p>
              <p class="infor">{{$t('base.RefundOrderDate')}}: {{$t(beTime(details.CreateDate), 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}}&nbsp;{{beTime(details.CreateDate, 'hh:mm')}}</p>
            </div>
          </hs-cell-1>
        </hs-section>
        <hs-section v-if="details.Segments && !noValue(JSON.stringify(details.Segments))">
          <div v-for="(item, index) in details.Segments" :key="index" style="width: 100%">
            <hs-cell-1 :showLogo="false">
              <div class="travel-details">
                <div class="time">
                  <div class="mark" :type="language">{{_showSegmentTitle(index)}}</div>
                  <p>{{$t(beTime(item.DepartDate), 'date', ['MM月dd日', 'MM dd'])}} {{getWeeks(beTime(item.DepartDate))}} {{item.DepartCityName}}&nbsp;-&nbsp;{{item.ArrivalCityName}}</p>
                </div>
                <div class="plane">
                  <div class="logo" :style="{'background-image': `url(${item.AirLineLogoUrl})`}"></div>
                  <p>{{item.AirLineName}}<template>&nbsp;<span v-if="!noValue(item.ActualCarryFlightNo)">{{$t('base.CodeShare')}}</span></template>&nbsp;{{item.FlightNo}}<template v-if="!noValue(item.Plane)">&nbsp;|&nbsp;{{item.Plane}}</template><template v-if="!noValue(item.Meal)">&nbsp;|&nbsp;{{item.Meal}}</template>
                  </p>
                </div>
                <div class="plane">
                  <p v-if="!noValue(item.ActualCarryFlightNo)">{{$t('base.OperatedBy')}}&nbsp;{{item.ActualCarryFlightNo}}</p>
                </div>
                <div class="details">
                  <div class="source">
                    <p>{{beTime(item.DepartDate, 'hh:mm')}}</p>
                    <p>{{item.DepartAirportName}} {{item.DepartTerminal}}</p>
                  </div>
                  <div class="to">
                    <div class="stopOver-logo" v-if="item.StopOver">
                      <p>{{$t('base.By')}}</p>
                    </div>
                    <div class="logo" v-else></div>
                    <div class="dashed"></div>
                    <p class="stopOver" v-if="item.StopOver">{{item.StopOver.StopCity}}</p>
                  </div>
                  <div class="destination">
                    <p>{{beTime(item.ArrivalDate, 'hh:mm')}}<span v-if="item.AddDays > 0">+{{item.AddDays}}</span></p>
                    <p>{{item.ArrivalAirportName}} {{item.ArrivalTerminal}}</p>
                  </div>
                </div>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" @click.native="_getFlightCabinRule(item.Rule)">
              <div class="cost-details">
                <p>{{$t('base.TicketRestrictions')}} ></p>
              </div>
            </hs-cell-1>
          </div>
        </hs-section>

        <!-- 乘客 -->
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="custom-cell-people">
              <div class="left">
                <p>{{$t('base.FlightsPassenger')}}</p>
              </div>
              <div class="right" :style="{'border-bottom-width': '0px'}">
                <div class="name">{{details.Passenger.Name}}</div>
                <div class="others">
                  <p v-if="details.Passenger.CredentialNo">{{details.Passenger.CredentialName}} {{details.Passenger.CredentialNo}}</p>
                  <p>{{$t('base.CellphoneNumber')}} {{details.Passenger.Mobile ? details.Passenger.Mobile : $t('base.Unfilled')}}</p>
                  <p v-if="details.Passenger.TicketNo">{{$t('base.TicketNumber')}} {{details.Passenger.TicketNo}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <!-- 乘客 end -->

        <!-- 退票原因 -->
        <hs-section v-if="details.RefundType === 0 || details.RefundType === 1">
          <hs-cell-small-title v-if="details.RefundType === 0"
            :title="$t('base.VoluntaryRefund')"
            type="disabled-input"
            :value="$t('base.ChargeByAirlineRefundRules')"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="details.RefundType === 1"
            :title="$t('base.InvoluntaryRefund')"
            type="disabled-input"
            :value="$t('base.FlightCancellationDelayForMoreThan')"
            :showLogo="false">
          </hs-cell-small-title>
        </hs-section>
        <!-- 退票原因 end -->

        <!-- 联系人 -->
        <hs-section>
          <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in details.Contacts" :key="index">
            <div class="custom-cell-people">
              <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
                <p>{{$t('base.ContactInformation')}}</p>
              </div>
              <div class="right" :style="{'border-bottom-width': index === details.Contacts.length - 1 ? '0px' : '1px'}">
                <div class="name">{{item.Name}}</div>
                <div class="others">
                  <p>{{$t('base.CellphoneNumber')}} {{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                  <p>{{$t('base.Email')}} {{item.Email ? item.Email : $t('base.Unfilled')}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <!-- 联系人 end -->

      </ul>
    </hs-scroll>
    <div class="footer" @click="_switchDetailsDialog">
      <div class="price" v-if="details.RefundStatus === 4">
        <p>{{$t('base.RefundAmount')}}</p>
        <p>￥{{details.RefundToGuestAmount}}</p>
      </div>
      <div v-else></div>
      <div class="details">
        <div class="logo" :style="{transform: showDetailsDialog ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
      </div>
    </div>
    <hs-dialog v-model="showDetailsDialog" height="calc(100% - 50px)" ref="detailsDialog">
      <hs-scroll class="detailsDialog" :data="[details]" @click.native.stop>
        <ul>
          <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.TicketAmount')}}</p>
                <p><span>￥{{details.TicketPrice}}</span><span>x 1{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.RefundFee')}}</p>
                <p v-if="details.RefundPrice === 0">{{$t('base.AirlinePassengerMaintenance')}}</p>
                <p v-else><span>-￥{{details.RefundPrice}}</span><span>x 1{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <!-- <hs-cell-1 :showLogo="false">
              <div class="detailsDialog-custom-cell">
                <p>服务费</p>
                <p><span>￥{{details.ServicePrice}}</span><span>x 1人</span></p>
              </div>
            </hs-cell-1> -->
          </hs-section>
          <div class="detailsDialog-tips">
            <p>{{$t('base.TheRefundFeeIsSubjectToTheReviewOfTheAirlineCompany')}}</p>
          </div>
        </ul>
      </hs-scroll>
    </hs-dialog>
    <c-return-explain v-model="showExplain" :details="returnExplainDetails"></c-return-explain>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import HsDialog from 'base/hs-dialog/hs-dialog'
import CReturnExplain from 'components/c-return-explain/c-return-explain'
import eventBus from 'common/js/eventBus.js'
import {kpiFlightGetRefundOrderDetail, kpiFlightCancelRefundOrder} from 'api'
export default {
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1,
    HsCellSmallTitle,
    HsDialog,
    CReturnExplain
  },
  created() {
    this._getOrderDetails()
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      details: {},
      showDetailsDialog: false,
      showExplain: false,
      returnExplainDetails: {}
    }
  },
  methods: {
    _getOrderDetails() {
      let params = {
        id: this.$route.query.Id
      }
      kpiFlightGetRefundOrderDetail(params).then((res) => {
        console.log('退票详情: ', res)
        this.details = res.ResultData
      })
    },
    _switchDetailsDialog() {
      if (this.showDetailsDialog) {
        this.$refs.detailsDialog.showDetails = false
      } else {
        this.showDetailsDialog = true
      }
    },
    // 根据航段显示单程/往返程/N程
    _showSegmentTitle(index) {
      let length = this.details.Segments && !this.noValue(JSON.stringify(this.details.Segments)) ? this.details.Segments.length : 0
      if (length === 1) {
        return this.$t('base.OneWay')
      } else if (length === 2) {
        if (this.details.Segments[0].DepartCityName === this.details.Segments[1].ArrivalCityName) {
          return index === 0 ? this.$t('base.Going') : this.$t('base.Return')
        } else {
          return this.$t('base.NoXSegment', 'function', index + 1)
        }
      } else {
        return this.$t('base.NoXSegment', 'function', index + 1)
      }
    },
    // 退改签信息
    _getFlightCabinRule(rule) {
      this.showExplain = true
      this.returnExplainDetails = {
        vhtml: rule
      }
    },
    /** 取消 */
    _cancel(id) {
      this.$alert({
        content: this.$t('TM.AreYouSureToCancelTheOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {
            id: id
          }
          kpiFlightCancelRefundOrder(params).then((res) => {
            if (res.ResultData === true) {
              this.$toast(this.$t('TM.OrderCancelled'))
              setTimeout(() => { eventBus.$emit(`refresh`) }, 0)
              this.$router.replace({path: '/personal/refundList'})
            } else {
              this.$toast(this.$t('TM.OrderCancelFailed'))
            }
          })
        }
      })
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.table-view
    width 100%
    height calc(100% - 45px - 50px)
    overflow hidden
    .state-details
      width 100%
      padding 15px 0
      background-color $color_white
      >.header
        @extend .flex-row
        @extend .flex-between
        width 100%
        margin-bottom 8px
        >p
          &:last-child
            visibility hidden
      >.infor
        width 100%
        font-size 0.8rem
        color $color_gray_text
        line-height 1.4em
      >.buttons
        @extend .flex-row
        width 100%
        >button
          flex 1
          background-color $color_theme
          color $color_white
          font-size 1rem
          padding 5px 0
          border-radius 4px
          margin 10px 5px 0 5px
        >button[type="common"]
          flex 1
          background-color $color_white
          color $color_theme
          border 1px solid $color_theme
          font-size 1rem
          padding 5px 0
          border-radius 4px
          margin 10px 5px 0 5px
    .travel-details
      width 100%
      padding-top 15px
      >.time
        @extend .flex-row
        @extend .flex-start
        width 100%
        margin-bottom 5px
        >.mark
          @extend .flex-center
          font-size 0.7rem
          border-radius 2px
          border 1px solid $color_theme
          color $color_theme
          padding 3px 0
          width 40px
          margin-right 10px
        >.mark[type="EN"]
          @extend .no-wrap
          width auto
          padding 3px
        >p
          width calc(100% - 50px)
          line-height 1.3em
      >.plane
        @extend .flex-row
        @extend .flex-start
        width 100%
        font-size 0.8rem
        color $color_gray_text
        line-height 1.6em
        >.logo
          width 15px
          height 15px
          background-size 100% 100%
          margin-right 3px
        >p
          >span
            color $color_theme
      >.details
        @extend .flex-row
        @extend .flex-between
        width 100%
        padding 15px 0
        >.source
        >.destination
          @extend .flex-column
          @extend .flex-center
          width 100px
          >p
            text-align center
            &:nth-child(1)
              font-size 1.2rem
              font-weight bold
              position relative
              line-height 1.3em
              >span
                color $color_theme
                font-size 0.8rem
                position absolute
                top -4px
                right -16px
            &:nth-child(2)
              font-size 0.8rem
              line-height 1.3em
        >.to
          @extend .flex-center
          width calc(100% - 200px)
          position relative
          >.logo
            width 15px
            height 15px
            background-size 100% 100%
            background-image url('../../../../../static/image/home/trip_plane.png')
            background-color $color_white
          >.dashed
            width 100%
            height 0px
            position absolute
            left 0
            top 50%
            border-top 1px dashed $color_black_text_0
          >.stopOver
            position absolute
            top 18px
            left 0
            font-size 0.6rem
            color $color_theme
            width 100%
            text-align center
          >.stopOver-logo
            @extend .flex-center
            width 24px
            height 18px
            background-color $color_white
            z-index 9
            >p
              text-align center
              width 18px
              line-height 1.2em
              color $color_theme
              font-size 0.6rem
    .cost-details
      @extend .flex-row
      @extend .flex-end
      width 100%
      font-size 0.9rem
      color $color_gray_text

    // 乘客信息
    .custom-cell-people
      @extend .flex-between
      @extend .flex-start
      @extend .flex-top
      width 100%
      >.left
        width 90px
        padding 15px 0
        >p
          @extend .flex-row
          @extend .flex-start
          font-size 1rem
          color $color_black_text_1
          line-height 1.4em
      >.right
        width calc(100% - 100px)
        padding 15px 0
        border-bottom 1px dashed $color_gray_layer
        >.name
          @extend .flex-row
          @extend .flex-start
          font-size 1rem
          font-weight bold
          line-height 1.4em
        >.others
          width 100%
          >p
            line-height 1.5em
            color $color_gray_text
            font-size 1rem
            &:first-child
              margin-top 7px
          >.insure
            font-size 0.8rem
  .detailsDialog
    position absolute !important
    width 100%
    left 0
    bottom 0
    background-color $color_white
    max-height 70%
    overflow hidden
    .detailsDialog-custom-cell
      @extend .flex-row
      @extend .flex-between
      width 100%
      height 44px
      >p
        font-size 0.8rem
        &:nth-child(1)
          color $color_black_text_0
        &:nth-child(2)
          color $color_theme
          >span
            &:nth-child(2)
              color $color_black_text_0
              margin-left 5px
    .detailsDialog-tips
      @extend .flex-row
      @extend .flex-end
      width 100%
      padding 10px 15px
      color $color_theme
      font-size 0.8rem
  >.footer
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 50px
    background-color $color_white
    border-top 1px solid $color_gray_layer
    padding 0 15px
    >.price
      @extend .flex-row
      @extend .flex-start
      width calc(100% - 30px)
      height 100%
      >p
        font-size 1rem
        &:nth-child(2)
          color $color_theme
          margin-left 5px
    >.details
      @extend .flex-row
      @extend .flex-between
      width 20px
      height 100%
      >p
        font-size 0.9rem
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        transition all 300ms
</style>



